.modal_backdrop{
    position: fixed;
    left: 0;
    top: 0;

    width: 100vw;
    height: 100vh;
    z-index: 10;

    background-color: var(--color-bgc-modal);
}

.modal_content{
    width: 700px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

.modal_img{
    height: 100%;
    width: 100%;
}
