.load_more{
    width:170px;

    margin: 10px auto;
    padding: 5px;

    background-color: var(--color-bgc);
    color: var(--color-text);


    border-radius: 4px;
    outline: none;
    border: none;    
}

.search_btn{
    border-top-left-radius: 4px;
    border-bottom-left-radius: 4px;
    outline: none;
    border: none;

    width:50px;
    height: 35px;

    cursor: pointer;
}

