.searchbar{
    position: fixed;
    z-index: 2;

    width: 100vw;
    height: 35px;

    background-color: var(--color-bgc);

    padding: 25px 0;
}

.search_form{
    display: flex;
    justify-content: center;
    align-items: center;
}

.search_form_input{
    width: 300px;
    height: 33px;

    outline: none;
    border: none;

    border-bottom-right-radius: 4px;
    border-top-right-radius: 4px;
}